img {
  user-select: none;
}
/* Hide scrollbar for Chrome, Safari and Opera */
div::-webkit-scrollbar {
  display: none;
}
/* Hide scrollbar for IE, Edge and Firefox */
div {
  -ms-overflow-style: none;
  /* IE and Edge */
  scrollbar-width: none;
  /* Firefox */
}
.App {
  overflow-x: hidden;
  text-align: center;
  width: 100vw;
  font-family: "VT323", monospace;
}
.App .footer {
  font-size: 24px;
  margin-top: 20px;
  color: #fff;
  padding: 40px 40px 50px;
}
.App-link {
  color: #61dafb;
}
@media only screen and (max-device-width: 480px) {
  .App .Index .banner {
    width: 90vw;
  }
  .App .Index .banner .typer {
    height: calc(50.6245472vw - 30px);
    margin: 15px;
    width: calc(90vw - 30px);
  }
  .App .Index .open-mint {
    margin-top: 10px;
    padding: 20px;
  }
  .App .Index .open-mint .open-mint-time-title {
    font-size: 2rem;
  }
  .App .Index .number-count {
    font-size: 5rem;
  }
  .App .Index .bothead-show .bothead {
    width: 80vw;
  }
}
