.About {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  font-size: calc(10px + 2vmin);
  color: white;
}
.About .banner {
  margin-top: 40px;
  width: 70vw;
  transition: all 0.3s linear;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.About .banner .typer {
  overflow-x: hidden;
  text-align: left;
  user-select: none;
  color: #ffee08;
  margin: 30px;
  width: calc(70vw - 60px);
  transition: all 0.3s linear;
}
.About .banner:hover .banner-img {
  filter: blur(0px);
}
