.Index {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.Index .banner {
  margin: 40px 0;
  width: 25vw;
  transition: all 0.3s linear;
  display: flex;
  justify-content: flex-start;
}
.Index .banner .banner-img {
  position: relative;
  transition: all 0.3s linear;
  top: 0;
}
.Index .banner:hover .banner-img {
  filter: blur(0px);
}
.Index .open-mint:hover {
  background: #ffee08;
  transition: all 0.5s linear;
}
.Index .open-mint:hover .open-mint-time-title {
  user-select: none;
  color: #001417;
}
.Index .open-mint:hover .number-count {
  color: #001417;
}
.Index .open-mint {
  margin-top: 20px;
  padding: 40px;
  position: relative;
}
.Index .open-mint #triangle {
  position: absolute;
  top: -20px;
  transform: rotate(45deg);
  left: -20px;
  width: 0;
  height: 0;
  border: 20px solid transparent;
  border-right: 20px solid #001417;
}
.Index .open-mint .open-mint-time-title {
  font-size: 3rem;
  color: #ffee08;
  font-family: "Share Tech Mono", monospace;
}
.Index .open-mint .number-count {
  margin-top: 10px;
  user-select: none;
  letter-spacing: 10px;
  font-family: "Qahiri", sans-serif;
  font-size: 6rem;
  color: #ffee08;
  filter: drop-shadow(0 0 0.55rem rgba(138, 10, 10, 0.6));
}
.Index .bothead-show:hover {
  filter: drop-shadow(0 0 0.75rem #ffee08);
}
.Index .bothead-show {
  transition: all 0.2s linear;
  margin: 60px 0;
  width: 25vw;
  height: 25vw;
  min-width: 450px;
  min-height: 450px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.Index .bothead-show .bothead {
  width: 25vw;
  min-width: 450px;
  pointer-events: none;
  user-select: none;
  filter: drop-shadow(0 0 0.75rem #ffee08);
  position: absolute;
}
.Index .bothead-show .no-show {
  visibility: hidden;
}
.Index .mint-button {
  margin-bottom: 40px;
}
