@import url("https://fonts.googleapis.com/css2?family=Orbitron:wght@900&display=swap");
.cyber-title {
  width: 100%;
  display: flex;
  user-select: none;
  align-items: center;
  justify-content: space-around;
}
.cyber-title .navi-text {
  font-family: "VT323", monospace;
  display: flex;
  align-items: center;
  color: #fff;
  font-size: 32px;
}
.cyber-title .navi-text .navi-item {
  transition: 0.2s all linear;
  padding: 12px;
}
.cyber-title .navi-text .navi-item:last-child {
  margin-left: 24px;
}
.cyber-title .about:hover {
  color: #001417;
  background: #ffee08;
}
.cyber-title .roadMap:hover {
  color: #001417;
  background: #ffee08;
}
.cyber-title .social-link {
  display: flex;
  align-items: center;
  margin: 8px 0 16px;
}
.cyber-title .social-link a {
  display: flex;
  align-items: center;
  color: #fff;
}
.cyber-title .social-link .twitter {
  display: flex;
  align-items: center;
  margin-right: 16px;
}
.cyber-title .social-link .twitter .twitter-logo {
  margin-right: 8px;
  width: 32px;
}
.cyber-title .social-link .discord {
  display: flex;
  align-items: center;
  margin-left: 16px;
}
.cyber-title .social-link .discord .discord-logo {
  margin-right: 8px;
  width: 32px;
}
.title-text {
  position: relative;
}
.cyber-title-text {
  margin: 0;
  color: #ffee08;
  text-transform: uppercase;
  font-size: 3rem;
  font-family: "Orbitron";
  font-weight: 900;
  letter-spacing: -1px;
  padding: 2rem;
  white-space: nowrap;
}
.cyber-title-text:before,
.cyber-title-text:after {
  position: absolute;
  content: attr(data-text);
  padding: 2rem;
  top: 0;
  left: 0;
  color: #ffee08;
  overflow: hidden;
  white-space: nowrap;
}
.cyber-title-text:before {
  left: 3px;
  text-shadow: -3px 2px #87cefa;
  animation: glitch-1 3s ease-in reverse infinite;
}
.cyber-title-text:after {
  left: -3px;
  text-shadow: -5px 2px #fff;
  animation: glitch-2 3s ease-in-out reverse infinite;
}
@keyframes glitch-1 {
  0% {
    clip: rect(132px, auto, 101px, 30px);
  }
  5% {
    clip: rect(17px, auto, 94px, 30px);
  }
  10% {
    clip: rect(40px, auto, 66px, 30px);
  }
  15% {
    clip: rect(87px, auto, 82px, 30px);
  }
  20% {
    clip: rect(137px, auto, 61px, 30px);
  }
  25% {
    clip: rect(34px, auto, 14px, 30px);
  }
  30% {
    clip: rect(133px, auto, 74px, 30px);
  }
  35% {
    clip: rect(76px, auto, 107px, 30px);
  }
  40% {
    clip: rect(59px, auto, 130px, 30px);
  }
  45% {
    clip: rect(29px, auto, 84px, 30px);
  }
  50% {
    clip: rect(22px, auto, 67px, 30px);
  }
  55% {
    clip: rect(67px, auto, 62px, 30px);
  }
  60% {
    clip: rect(10px, auto, 105px, 30px);
  }
  65% {
    clip: rect(78px, auto, 115px, 30px);
  }
  70% {
    clip: rect(105px, auto, 13px, 30px);
  }
  75% {
    clip: rect(15px, auto, 75px, 30px);
  }
  80% {
    clip: rect(66px, auto, 39px, 30px);
  }
  85% {
    clip: rect(133px, auto, 73px, 30px);
  }
  90% {
    clip: rect(36px, auto, 128px, 30px);
  }
  95% {
    clip: rect(68px, auto, 103px, 30px);
  }
  100% {
    clip: rect(14px, auto, 100px, 30px);
  }
}
@keyframes glitch-2 {
  0% {
    clip: rect(129px, auto, 36px, 30px);
  }
  5% {
    clip: rect(36px, auto, 4px, 30px);
  }
  10% {
    clip: rect(85px, auto, 66px, 30px);
  }
  15% {
    clip: rect(91px, auto, 91px, 30px);
  }
  20% {
    clip: rect(148px, auto, 138px, 30px);
  }
  25% {
    clip: rect(38px, auto, 122px, 30px);
  }
  30% {
    clip: rect(69px, auto, 54px, 30px);
  }
  35% {
    clip: rect(98px, auto, 71px, 30px);
  }
  40% {
    clip: rect(146px, auto, 34px, 30px);
  }
  45% {
    clip: rect(134px, auto, 43px, 30px);
  }
  50% {
    clip: rect(102px, auto, 80px, 30px);
  }
  55% {
    clip: rect(119px, auto, 44px, 30px);
  }
  60% {
    clip: rect(106px, auto, 99px, 30px);
  }
  65% {
    clip: rect(141px, auto, 74px, 30px);
  }
  70% {
    clip: rect(20px, auto, 78px, 30px);
  }
  75% {
    clip: rect(133px, auto, 79px, 30px);
  }
  80% {
    clip: rect(78px, auto, 52px, 30px);
  }
